<template>
    <div>
        <div>
            <div style="display: flex; justify-content: space-between">
                <div>
                    <el-input placeholder="请输入员工名进行搜索" prefix-icon="el-icon-search"
                              style="width: 350px; margin-right: 10px" size="small"
                              v-model="keyword" @keydown.enter.native="initEmps"
                              clearable
                              @clear="initEmps" :disabled="showAdvanceSearchView">

                    </el-input>
                    <el-button type="primary" @click="" icon="el-icon-search" size="small" @click="initEmps" :disabled="showAdvanceSearchView">搜索</el-button>
                    <el-button type="primary" @click="" size="small" @click="showAdvanceSearchView = !showAdvanceSearchView">
                        <i :class="showAdvanceSearchView?'fa fa-angle-double-up':'fa fa-angle-double-down'" aria-hidden="true"></i>
                        高级搜索
                    </el-button>
                </div>
                <div>
                    <el-upload
                            :on-success="onSuccess"
                            :on-error="onError"
                            :before-upload="beforeUpload"
                            :show-file-list="false"
                            :disabled="importDataDisabled"
                            style="display: inline-flex; margin-right: 8px"
                            action="/employee/basic/import">
                        <el-button :disabled="importDataDisabled" type="success" size="small" :icon="importDataButtonIcon">
                            {{importDataButtonText}}
                        </el-button>
                    </el-upload>

                    <el-button type="success" size="small" @click="exportDate" icon="el-icon-download">
                        导出数据
                    </el-button>
                    <el-button type="success" icon="el-icon-plus" size="small" @click="showAddEmpView">
                        添加用户
                    </el-button>
                </div>
            </div>
            <!--过渡动画设置-->
            <transition name="slide-fade">
                <div v-show="showAdvanceSearchView" style="border: 1px solid #13548A; border-radius: 5px; box-sizing: border-box; padding: 5px; margin: 10px 0px">
                <el-row>
                    <el-col :span="5">
                        政治面貌：
                        <el-select v-model="searchValues.politicId" placeholder="政治面貌" size="mini" style="width: 130px">
                            <el-option
                                    v-for="item in politicsstatus"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="4">
                        民族：
                        <el-select v-model="searchValues.nationId" placeholder="民族" size="mini" style="width: 130px">
                            <el-option
                                    v-for="item in nations"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="4">
                        职位：
                        <el-select v-model="searchValues.posId" placeholder="请选择职位" size="mini" style="width: 130px">
                            <el-option
                                    v-for="item in positions"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="4">
                        职称：
                        <el-select v-model="searchValues.jobLevelId" placeholder="请选择职称" size="mini"
                                   style="width: 130px">
                            <el-option
                                    v-for="item in jobLevels"
                                    :key="item.id"
                                    :label="item.name"
                                    :value="item.id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="7">
                        聘用形式：
                        <el-radio-group v-model="searchValues.engageForm">
                            <el-radio label="劳动合同">劳动合同</el-radio>
                            <el-radio label="劳务合同">劳务合同</el-radio>
                        </el-radio-group>
                    </el-col>
                </el-row>
                <el-row style="margin-top:10px">
                    <el-col :span="5">
                        所属部门：
                        <el-popover
                                placement="right"
                                title="请选择部门"
                                width="220"
                                trigger="manual"
                                v-model="popVisible2">
                            <el-tree default-expand-all :data="allDeps" :props="defaultProps"
                                     @node-click="searchViewHandleNodeClick"></el-tree>
                            <div style="width: 130px; display: inline-flex; font-size: 13px;
                                            border: 1px solid #dedede;
                                            height: 24px; border-radius: 4px; margin-top: 8px;
                                            cursor: pointer; align-items: center; padding-left: 8px;box-sizing: border-box;"
                                 @click="showDepView2"
                                 slot="reference">{{inputDepName2}}</div>
                        </el-popover>
                    </el-col>
                    <el-col :span="10">
                        入职日期：
                        <el-date-picker
                                v-model="searchValues.beginDataScope"
                                size="mini"
                                unlink-panels
                                type="daterange"
                                value-format="yyyy-MM-dd"
                                range-separator="至"
                                start-placeholder="开始日期"
                                end-placeholder="结束日期">
                        </el-date-picker>
                    </el-col>
                    <el-col :span="5" :offset="4">
                        <el-button size="mini" @click="showAdvanceSearchView = !showAdvanceSearchView">取消</el-button>
                        <el-button size="mini" icon="el-icon-search" type="primary" @click="initEmps('advanced')">搜索</el-button>
                    </el-col>
                </el-row>
            </div>
            </transition>
        </div>
        <div style="margin-top: 10px">
            <el-table
                    v-loading="loading"
                    :data="emps"
                    stripe
                    border
                    @selection-change="handleSelectionChange"
                    style="width: 100%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        fixed
                        align="left"
                        label="姓名"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="workId"
                        label="工号"
                        align="left"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="gender"
                        label="性别"
                        align="left"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="birthday"
                        align="left"
                        label="出生日期"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="idCard"
                        align="left"
                        label="身份证号码"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="wedlock"
                        label="婚姻状况"
                        width="85">
                </el-table-column>
                <el-table-column
                        prop="nation.name"
                        label="民族"
                        width="50">
                </el-table-column>
                <el-table-column
                        prop="nativePlace"
                        align="left"
                        label="籍贯"
                        width="80">
                </el-table-column>
                <el-table-column
                        prop="politicsStatus.name"
                        label="政治面貌">
                </el-table-column>
                <el-table-column
                        prop="email"
                        align="left"
                        label="电子邮件"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="phone"
                        align="left"
                        label="电话号码"
                        width="130">
                </el-table-column>
                <el-table-column
                        prop="address"
                        align="left"
                        label="联系地址"
                        width="220">
                </el-table-column>
                <el-table-column
                        prop="department.name"
                        align="left"
                        label="所属部门"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="position.name"
                        align="left"
                        label="职位"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="jobLevel.name"
                        align="left"
                        label="职称"
                        width="110">
                </el-table-column>
                <el-table-column
                        prop="engageForm"
                        align="left"
                        label="聘用形式"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="beginDate"
                        align="left"
                        label="入职日期"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="conversionTime"
                        align="left"
                        label="转正日期"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="beginContract"
                        align="left"
                        label="合同起始日期"
                        width="120">
                </el-table-column>
                <el-table-column
                        prop="endContract"
                        align="left"
                        label="合同终止日期"
                        width="120">
                </el-table-column>
                <el-table-column
                        align="left"
                        label="合同期限"
                        width="80">
                    <template slot-scope="scope">
                        {{scope.row.contractTerm}}年
                    </template>
                </el-table-column>
                <el-table-column
                        prop="tiptopDegree"
                        label="最高学历"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="workState"
                        label="在职状态"
                        width="90">
                </el-table-column>
                <el-table-column
                        prop="school"
                        label="毕业院校"
                        width="150">
                </el-table-column>
                <el-table-column
                        prop="specialty"
                        label="专业"
                        width="150">
                </el-table-column>
                <el-table-column
                        fixed="right"
                        width="100"
                        label="操作">
                    <template slot-scope="scope">
                        <el-button style="padding: 3px" size="mini" @click="showEditEmpView(scope.row)">编辑</el-button>
<!--                        <el-button style="padding: 3px" size="mini" type="primary">查看高级资料</el-button>-->
                        <el-button style="padding: 3px" size="mini" type="danger" @click="deleteEmp(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div style="display: flex; justify-content: flex-end; margin-top: 2px">
            <el-button type="danger" size="small" class="deleteBatch" @click="deleteBasicByIds"
                       :disabled="multipleSelectEmps.length===0">
                批量删除
            </el-button>

            <el-pagination
                    background
                    @current-change="currentChange"
                    @size-change="sizeChange"
                    layout="sizes, prev, pager, next, jumper, ->, total, slot"
                    :total="total">
            </el-pagination>
        </div>
        <el-dialog
                :title="title"
                :visible.sync="dialogVisible"
                width="80%">
            <div>
                <el-form :model="emp" :rules="rules" ref="empForm">
                    <el-row>
                        <el-col :span="6">
                            <el-form-item label="姓名：" prop="name">
                                <el-input size="mini" style="width: 150px" prefix-icon="el-icon-edit" v-model="emp.name"
                                          placeholder="请输入员工姓名"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="5">
                            <el-form-item label="性别：" prop="gender">
                                <el-radio-group v-model="emp.gender" style="margin-top: 12px">
                                    <el-radio label="男">男</el-radio>
                                    <el-radio label="女">女</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="出生日期：" prop="birthday">
                                <el-date-picker
                                        v-model="emp.birthday"
                                        size="mini"
                                        type="date"
                                        value-format="yyyy-MM-dd"
                                        style="width: 150px"
                                        placeholder="出身日期">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="政治面貌：" prop="politicId">
                                <el-select v-model="emp.politicId" placeholder="政治面貌" size="mini" style="width: 200px">
                                    <el-option
                                            v-for="item in politicsstatus"
                                            :key="item.id"
                                            :label="item.name"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="6">
                            <el-form-item label="民族：" prop="nationId">
                                <el-select v-model="emp.nationId" placeholder="民族" size="mini" style="width: 150px">
                                    <el-option
                                            v-for="item in nations"
                                            :key="item.id"
                                            :label="item.name"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="5">
                            <el-form-item label="籍贯：" prop="nativePlace">
                                <el-input size="mini" style="width: 150px" prefix-icon="el-icon-edit"
                                          v-model="emp.nativePlace" placeholder="员工籍贯"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="电子邮箱：" prop="email">
                                <el-input size="mini" style="width: 150px" prefix-icon="el-icon-message"
                                          v-model="emp.email" placeholder="员工邮箱"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="联系地址：" prop="address">
                                <el-input size="mini" style="width: 200px" prefix-icon="el-icon-edit"
                                          v-model="emp.address" placeholder="联系地址"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="6">
                            <el-form-item label="职位：" prop="posId">
                                <el-select v-model="emp.posId" placeholder="请选择职位" size="mini" style="width: 150px">
                                    <el-option
                                            v-for="item in positions"
                                            :key="item.id"
                                            :label="item.name"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="5">
                            <el-form-item label="职称：" prop="jobLevelId">
                                <el-select v-model="emp.jobLevelId" placeholder="请选择职称" size="mini"
                                           style="width: 150px">
                                    <el-option
                                            v-for="item in jobLevels"
                                            :key="item.id"
                                            :label="item.name"
                                            :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="所属部门：" prop="departmentId">
                                <el-popover
                                        placement="right"
                                        title="请选择部门"
                                        width="220"
                                        trigger="manual"
                                        v-model="popVisible">
                                    <el-tree default-expand-all :data="allDeps" :props="defaultProps"
                                             @node-click="handleNodeClick"></el-tree>
                                    <div style="width: 150px; display: inline-flex; font-size: 13px;
                                            border: 1px solid #dedede;
                                            height: 24px; border-radius: 4px; margin-top: 8px;
                                            cursor: pointer; align-items: center; padding-left: 8px;box-sizing: border-box"
                                         @click="showDepView"
                                         slot="reference">
                                        {{inputDepName}}
                                    </div>
                                </el-popover>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="电话号码：" prop="phone">
                                <el-input size="mini" style="width: 200px" prefix-icon="el-icon-phone"
                                          v-model="emp.phone" placeholder="电话号码"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="6">
                            <el-form-item label="工号：" prop="workId">
                                <el-input size="mini" style="width: 150px"
                                          prefix-icon="el-icon-edit"
                                          v-model="emp.workId"
                                          placeholder="工号" disabled></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="5">
                            <el-form-item label="学历：" prop="tiptopDegree">
                                <el-select v-model="emp.tiptopDegree" placeholder="请选择学历" size="mini"
                                           style="width: 150px">
                                    <el-option
                                            v-for="item in tiptopDegrees"
                                            :key="item"
                                            :label="item"
                                            :value="item">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="毕业院校：" prop="school">
                                <el-input size="mini" style="width: 150px" prefix-icon="el-icon-edit"
                                          v-model="emp.school" placeholder="请输入毕业院校"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="专业名称：" prop="specialty">
                                <el-input size="mini" style="width: 200px" prefix-icon="el-icon-edit"
                                          v-model="emp.specialty" placeholder="请输入专业名称"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="6">
                            <el-form-item label="入职日期：" prop="beginDate">
                                <el-date-picker
                                        v-model="emp.beginDate"
                                        size="mini"
                                        type="date"
                                        value-format="yyyy-MM-dd"
                                        style="width: 130px"
                                        placeholder="入职日期">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="转正日期：" prop="conversionTime">
                                <el-date-picker
                                        v-model="emp.conversionTime"
                                        size="mini"
                                        type="date"
                                        value-format="yyyy-MM-dd"
                                        style="width: 130px"
                                        placeholder="转正日期">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="合同起始日期：" prop="beginContract">
                                <el-date-picker
                                        v-model="emp.beginContract"
                                        size="mini"
                                        type="date"
                                        value-format="yyyy-MM-dd"
                                        style="width: 130px"
                                        placeholder="合同起始日期">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="合同终止日期：" prop="endContract">
                                <el-date-picker
                                        v-model="emp.endContract"
                                        size="mini"
                                        type="date"
                                        value-format="yyyy-MM-dd"
                                        style="width: 130px"
                                        placeholder="合同终止日期">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="身份证号码：" prop="idCard">
                                <el-input size="mini" style="width: 180px" prefix-icon="el-icon-edit"
                                          v-model="emp.idCard" placeholder="请输入身份证号码"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="聘用形式：" prop="engageForm">
                                <el-radio-group v-model="emp.engageForm" style="margin-top: 13px">
                                    <el-radio label="劳动合同">劳动合同</el-radio>
                                    <el-radio label="劳务合同">劳务合同</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="婚姻状况：" prop="wedlock">
                                <el-radio-group v-model="emp.wedlock" style="margin-top: 13px">
                                    <el-radio label="已婚">已婚</el-radio>
                                    <el-radio label="未婚">未婚</el-radio>
                                    <el-radio label="离异">离异</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="doAddEmp">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "EmpBasic",
        data() {
            return {
                multipleSelectEmps: [],
                searchValues: {
                    politicId: null,
                    nationId: null,
                    jobLevelId: null,
                    posId: null,
                    engageForm: null,
                    departmentId: null,
                    beginDataScope: null
                },
                inputDepName2: '',
                popVisible2: false,
                showAdvanceSearchView: false,
                importDataDisabled: false,
                importDataButtonText: '导入数据',
                importDataButtonIcon: 'el-icon-upload2',
                title: '',
                allDeps: [],
                popVisible: false,
                tiptopDegrees: ['本科', '大专', '硕士', '博士', '高中', '初中', '小学', '其他'],
                nations: [],
                jobLevels: [],
                politicsstatus: [],
                positions: [],
                dialogVisible: false,
                emps: [],
                loading: false,
                keyword: '',
                page: 1,
                size: 10,
                total: 0,
                inputDepName: '所属部门',
                emp: {
                    name: "",
                    gender: "",
                    birthday: "",
                    idCard: "",
                    wedlock: "",
                    nationId: null,
                    nativePlace: "",
                    politicId: null,
                    email: "",
                    phone: "",
                    address: "",
                    departmentId: null,
                    jobLevelId: null,
                    posId: null,
                    engageForm: "",
                    tiptopDegree: "",
                    specialty: "",
                    school: "",
                    beginDate: "",
                    workState: "在职",
                    workId: "",
                    contractTerm: 2,
                    conversionTime: "",
                    notworkDate: null,
                    beginContract: "",
                    endContract: "",
                    workAge: null,
                },
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                rules: {
                    name: [{required: true, message: '请输入姓名', trigger: 'blur'}],
                    gender: [{required: true, message: '请输入性别', trigger: 'blur'}],
                    birthday: [{required: true, message: '请输入出生日期', trigger: 'blur'}],
                    idCard: [{required: true, message: '请输入身份证号', trigger: 'blur'}, {
                        pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
                        message: '身份证号码格式不正确',
                        trigger: 'blur'
                    }],
                    wedlock: [{required: true, message: '请输入婚姻状况', trigger: 'blur'}],
                    nationId: [{required: true, message: '请输入民族', trigger: 'blur'}],
                    nativePlace: [{required: true, message: '请输入籍贯', trigger: 'blur'}],
                    politicId: [{required: true, message: '请输入政治面貌', trigger: 'blur'}],
                    email: [{required: true, message: '请输入邮箱地址', trigger: 'blur'}, {
                        type: 'email',
                        message: '邮箱格式错误',
                        trigger: 'blur'
                    }],
                    phone: [{required: true, message: '请输入手机号码', trigger: 'blur'}],
                    address: [{required: true, message: '请输入地址信息', trigger: 'blur'}],
                    departmentId: [{required: true, message: '请输入所属部门', trigger: 'blur'}],
                    jobLevelId: [{required: true, message: '请输入职称', trigger: 'blur'}],
                    posId: [{required: true, message: '请输入职位', trigger: 'blur'}],
                    engageForm: [{required: true, message: '请输入聘用形式', trigger: 'blur'}],
                    tiptopDegree: [{required: true, message: '请输入最高学历', trigger: 'blur'}],
                    specialty: [{required: true, message: '请输入专业名称', trigger: 'blur'}],
                    school: [{required: true, message: '请输入毕业院校', trigger: 'blur'}],
                    beginDate: [{required: true, message: '请输入入职日期', trigger: 'blur'}],
                    workState: [{required: true, message: '请输入工作状态', trigger: 'blur'}],
                    workId: [{required: true, message: '请输入工号', trigger: 'blur'}],
                    contractTerm: [{required: true, message: '请输入合同期限', trigger: 'blur'}],
                    conversionTime: [{required: true, message: '请输入转正日期', trigger: 'blur'}],
                    notworkDate: [{required: true, message: '请输入离职日期', trigger: 'blur'}],
                    beginContract: [{required: true, message: '请输入合同起始日期', trigger: 'blur'}],
                    endContract: [{required: true, message: '请输入合同终止日期', trigger: 'blur'}],
                    workAge: [{required: true, message: '请输入工龄', trigger: 'blur'}]
                }
            }
        },
        mounted() {
            this.initEmps();
            this.initData();
            this.initPositions();
        },
        methods: {
            handleSelectionChange(val){
                this.multipleSelectEmps = val;
            },
            deleteBasicByIds(ids){
                this.$confirm('是否删除选中的员工信息? ', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = '?';
                    this.multipleSelectEmps.forEach(item=>{
                        ids += 'ids=' + item.id + '&';
                    })
                    this.deleteRequest("/employee/basic/" + ids).then(resp=>{
                        if(resp){
                            this.initEmps();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            searchViewHandleNodeClick(data){
                this.inputDepName2 = data.name;
                this.searchValues.departmentId = data.id;
                this.popVisible2 = !this.popVisible2;
            },
            onError(err, file, fileList){
                this.importDataButtonText = '导入数据';
                this.importDataButtonIcon = 'el-icon-upload2';
                this.importDataDisabled = false;
                this.initEmps();
            },
            onSuccess(response, file, fileList){
                this.importDataButtonText = '导入数据';
                this.importDataButtonIcon = 'el-icon-upload2';
                this.importDataDisabled = false;
            },
            beforeUpload(){
                this.importDataButtonText = '正在导入';
                this.importDataButtonIcon = 'el-icon-loading';
                this.importDataDisabled = true;
            },
            exportDate(){
                window.open('/employee/basic/export/', '_parent');
            },
            emptyEmp(){
                this.emp = {
                    name: "",
                    gender: "",
                    birthday: "",
                    idCard: "",
                    wedlock: "",
                    nationId: null,
                    nativePlace: "",
                    politicId: 13,
                    email: "",
                    phone: "",
                    address: "",
                    departmentId: null,
                    jobLevelId: null,
                    posId: null,
                    engageForm: "",
                    tiptopDegree: "",
                    specialty: "",
                    school: "",
                    beginDate: "",
                    workState: "在职",
                    workId: "",
                    contractTerm: 2,
                    conversionTime: "",
                    notworkDate: null,
                    beginContract: "",
                    endContract: "",
                    workAge: null
                }
                this.inputDepName = '';
            },
            showEditEmpView(data){
                this.initPositions();
                this.title='修改员工信息';
                this.emp = data;
                this.inputDepName=data.department.name;
                this.dialogVisible = true;
            },
            deleteEmp(data){
                this.$confirm('此操作将永久删除【' + data.name + '】, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                   this.deleteRequest("/employee/basic/" + data.id).then(resp=>{
                       if(resp){
                           this.initEmps();
                       }
                   })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            doAddEmp() {
                if(this.emp.id){
                    this.$refs['empForm'].validate(valid => {
                        if (valid) {
                            this.putRequest("/employee/basic/", this.emp).then(resp => {
                                if (resp) {
                                    this.dialogVisible = false;
                                    this.initEmps();
                                }
                            })
                        }
                    })
                }else{
                    this.$refs['empForm'].validate(valid => {
                        if (valid) {
                            this.postRequest("/employee/basic/", this.emp).then(resp => {
                                if (resp) {
                                    this.dialogVisible = false;
                                    this.initEmps();
                                }
                            })
                        }
                    })
                }
            },
            handleNodeClick(data) {
                this.inputDepName = data.name;
                this.emp.departmentId = data.id;
                this.popVisible = !this.popVisible;
            },
            showDepView() {
                this.popVisible = !this.popVisible;
            },
            showDepView2() {
                this.popVisible2 = !this.popVisible2;
            },
            getMaxWorkID() {
                this.getRequest("/employee/basic/maxWorkId").then(resp => {
                    if (resp) {
                        this.emp.workId = resp.obj;
                    }
                })
            },
            initPositions() {
                this.getRequest("/employee/basic/positions").then(resp => {
                    if (resp) {
                        this.positions = resp;
                    }
                })
            },

            initData() {
                if (!window.sessionStorage.getItem("nations")) {
                    this.getRequest("/employee/basic/nations").then(resp => {
                        if (resp) {
                            this.nations = resp;
                            window.sessionStorage.setItem("nations", JSON.stringify(resp));
                        }

                    });
                } else {
                    this.nations = JSON.parse(window.sessionStorage.getItem("nations"));
                }

                if (!window.sessionStorage.getItem("jobLevels")) {
                    this.getRequest("/employee/basic/jobLevels").then(resp => {
                        if (resp) {
                            this.jobLevels = resp;
                            window.sessionStorage.setItem("jobLevels", JSON.stringify(resp));
                        }
                    });
                } else {
                    this.jobLevels = JSON.parse(window.sessionStorage.getItem("jobLevels"));
                }

                if (!window.sessionStorage.getItem("politicsstatus")) {
                    this.getRequest("/employee/basic/politicsstatus").then(resp => {
                        if (resp) {
                            this.politicsstatus = resp;
                            window.sessionStorage.setItem("politicsstatus", JSON.stringify(resp));
                        }
                    });
                } else {
                    this.politicsstatus = JSON.parse(window.sessionStorage.getItem("politicsstatus"));
                }

                if (!window.sessionStorage.getItem("deps")) {
                    this.getRequest("/employee/basic/deps").then(resp => {
                        if (resp) {
                            this.allDeps = resp;
                            window.sessionStorage.setItem("deps", JSON.stringify(resp));
                        }
                    });
                } else {
                    this.allDeps = JSON.parse(window.sessionStorage.getItem("deps"));
                }
            },
            showAddEmpView() {
                this.emptyEmp();
                this.title = '添加员工';
                this.getMaxWorkID();
                this.dialogVisible = true;
            },
            sizeChange(currentSize) {
                this.size = currentSize;
                this.initEmps();
            },
            currentChange(currentPage) {
                this.page = currentPage;
                this.initEmps();
            },
            initEmps(type) {
                this.loading = true;
                let url = '/employee/basic/' + '?page=' + this.page + "&size=" + this.size;
                if(type && type == 'advanced'){//高级搜索
                    if(this.searchValues.politicId){
                        url += '&politicId=' + this.searchValues.politicId;
                    }
                    if(this.searchValues.nationId){
                        url += '&nationId=' + this.searchValues.nationId;
                    }
                    if(this.searchValues.jobLevelId){
                        url += '&jobLevelId=' + this.searchValues.jobLevelId;
                    }
                    if(this.searchValues.posId){
                        url += '&posId=' + this.searchValues.posId;
                    }
                    if(this.searchValues.engageForm){
                        url += '&engageForm=' + this.searchValues.engageForm;
                    }
                    if(this.searchValues.departmentId){
                        url += '&departmentId=' + this.searchValues.departmentId;
                    }
                    if(this.searchValues.beginDataScope){
                        url += '&beginDataScope=' + this.searchValues.beginDataScope;
                    }
                }else{ //普通搜索
                    url += "&name=" + this.keyword;
                }
                this.getRequest(url).then(resp => {
                    this.loading = false;
                    if (resp) {
                        this.emps = resp.data;
                        this.total = resp.total;
                    }
                });
            }
        }
    }
</script>

<style>
    .deleteBatch {
        margin-top: 2px;
        margin-right: 350px;
    }
    .slide-fade-enter-active {
        transition: all .8s ease;
    }
    .slide-fade-leave-active {
        transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    .slide-fade-enter, .slide-fade-leave-to {
        transform: translateX(10px);
        opacity: 0;
    }

</style>
